.card-instruction {
  display: flex;
  gap: 20px;
  position: relative;
  counter-increment: number;
}
@media (min-width: 768px) {
  .card-instruction {
    height: 280px;
    min-width: 230px;
    flex-direction: column;
    gap: 0;
    justify-content: space-evenly;
  }
  .card-instruction:nth-child(2n+1) {
    margin-right: 139px;
  }
  .card-instruction::before {
    content: counter(number);
    position: absolute;
    width: 112px;
    color: #f2f2f2;
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 280px;
    line-height: 1;
    z-index: -1;
    left: 118px;
  }
}
@media (min-width: 1440px) {
  .card-instruction:not(:last-child) {
    margin-right: 80px;
