.change {
  position: absolute;
  left: 50%;
  top: 0;
  width: 4px;
  height: 100%;
  background-color: #666666;
  border-radius: 2px;
}
.change:hover {
  background-color: #68b738;
}
.change:active {
  background-color: #68b738;
  opacity: 0.6;
}

.change__button {
  border: none;
  background: transparent;
  z-index: 3;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 0;
}

.change__icon {
  fill: #666666;
  padding: 9px 6px;
  background-color: #ffffff;
  border-radius: 100%;
  border: 2px solid #666666;
}
.change__icon:hover {
  fill: #68b738;
