}

.control {
  position: relative;
  line-height: 1.12;
}
.control:hover .control__mark {
  border: 1px solid #bababa;
}

.control__label {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.12;
  text-transform: uppercase;
  color: #222222;
  padding-left: 45px;
}
@media (min-width: 768px) {
  .control__label {
    font-size: 20px;
    line-height: 1;
    padding-left: 61px;
  }
}

.control__mark {
  display: block;
  position: absolute;
  width: 30px;
  height: 30px;
  top: 40%;
  transform: translateY(-50%);
  background: none;
  border: 1px solid #cdcdcd;
}
@media (min-width: 768px) {
  .control__mark {
    width: 40px;
    height: 40px;
    top: 32%;
  }
}
@media (min-width: 1440px) {
  .control__mark {
    top: 33%;
  }
}

.control__input[type=radio]:checked + .control__mark::before {
  position: absolute;
  content: "";
  width: 13px;
  height: 13px;
  background-color: #68b738;
  border-radius: 50%;
  top: 30%;
  transform: translateX(65%);
}
@media (min-width: 768px) {
  .control__input[type=radio]:checked + .control__mark::before {
    width: 18px;
    height: 18px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

.control__input[type=checkbox]:checked + .control__mark::before {
  position: absolute;
  content: "";
  width: 18px;
  height: 14px;
  background-image: url("../img/tick.svg");
  border-radius: 50%;
  top: 50%;
  transform: translateY(-50%);
  left: 25%;
}

.control__mark--round {
  border-radius: 50%;
}

